  --orangeLight:#ce8c36e3;
  --darkBlue:#1c1d24 ;
  --fontFam : 'Playfair Display', serif;
  /* --orangeLight:#e16328af;  */
}

.App {
  text-align: center;
  background-color: #131416;
  background-color: var(--black);
}

.sectionContainer{
    overflow: hidden;
    position: relative;
    
    height: 100vh;

}
.sectionBG{
      
  opacity: 0.1;
  position: absolute;
  left: 0;
  top: 0;
  /* width: fit-content; */
  height: auto;
  /* width: 100vw; */
  /* width: auto; */
  /* height: 100vh; */
  padding-bottom: 62%;
}

.textDiv{
    color: rgb(226, 223, 223);
    position: absolute;
    display: flex;
    flex-direction: column;
    text-align:justify;
    align-content: center;
    width: 400px;
    /* transform: translate(85%,50%); */
    left: 50%;
    top: 30%;
    margin-left: -160px;
    font-size: 1.5rem;
    font-family:  'Playfair Display', serif;
    position: relative;
    /* background:violet; */
    z-index: 10;
}

.specialText{
    font-size: 1.5rem;
    
    /* color:rgb(190, 127, 45) */
}

.animatedText{
    color:rgb(214, 145, 55);
    font-size: 2.5rem;  
}



.socialLinksUL {
    display: flex;
    position: absolute;
    width: 400px;
    top: 80%;
    left: 50%;
    margin-left: -200px;
    /* transform: translate(-50%, -50%); */
  }
  
.socialLinksUL li {
list-style: none;
}
  
  .socialLinksUL li a {
    width: 50px;
    height: 50px;
    background-color: rgb(194, 192, 192);
    text-align: center;
    display: flex;
    align-content: center;
    justify-content: center ;
    line-height: 55px;
    font-size: 30px;
    margin: 0 10px;
    display: block;
    border-radius: 50%;
    position: relative;
    overflow: hidden;
    border: 3px solid rgb(190, 190, 190);
    z-index: 1;
    transition: 0.6s ease all;
  }
  
  .socialLinksUL li a .icon {
    position: relative;
    /* position: absolute; */
    color: #262626;
    transition: .5s;
    z-index: 3;
  }
  .socialLinksUL li a:hover{
    transition: 0.5s ease all;
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    background:rgb(214, 145, 55);
    border-color: #262626;
  }
  .socialLinksUL li a:hover .icon {
    /* color: rgb(194, 192, 192); */
    color: #262626;
    -webkit-transform: rotateY(360deg);
            transform: rotateY(360deg);
    transition: 1s ease all;
    /* transform: scale(1.5); */
  }
  
  .socialLinksUL li a:before {
    content: "";
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgb(104, 74, 40);
    transition: .5s;
    z-index: 2;
  }
  
  .socialLinksUL li a:hover:before {
    top: 0;
    background:rgb(214, 145, 55);
  }
  
  
